<template>
  <div class="login">
    <div class="search-heard d-flex bg-white jc-between">
      <!--  -->
      <div> 
        <el-button type="text" @click="$router.go(-1)">
          <div class="iconfont icon-back ml-2 fs-xxl text-dark"></div>
        </el-button>
      </div>

      <div class="title fs-xl mt-3">
        
      </div>
      <div class="title fs-xl mt-3 pr-3">
        <router-link to="/register"></router-link>
      </div>
    </div>
    <mt-navbar v-model="selected">
      <mt-tab-item id="0">
        
      </mt-tab-item>
      <mt-tab-item id="1">
        
      </mt-tab-item>
    </mt-navbar>
    <mt-tab-container class="bg-white" v-model="selected">
      <mt-tab-container-item id="0">
        <div class="form-box">
          <div class="form-row">
            <div class="login-p">
            </div>
            <div class="input-box">
              <mt-field type="text" placeholder="" :class="checkPhone()" v-model="model.username" maxlength="11" />
            </div>
          </div>
          <div id="areaVer" v-show="area"></div>
          <div class="form-row">
            <div class="pwd-p">
            </div>
            <div class="input-box">
                <mt-field type="password" placeholder="" v-model="model.password" maxlength="20" />
            </div>
          </div>
          <div class="btn-row py-2" style="text-align: center;">
            <el-button type="danger" @click="login()"></el-button>
          </div>
        </div>
      </mt-tab-container-item>
      <mt-tab-container-item id="1">
        <div class="form-box">
          <div class="form-row">
            <div class="login-h">
            </div>
            <div class="input-box">
                <mt-field type="text" placeholder=""  v-model="phoneNumber" maxlength="11" />
            </div>
          </div>
          <div class="form-row">
            <div class="pwd-p">
            </div>
            <div class="input-box">
                <mt-field type="text" placeholder="" v-model="verify"  maxlength="6">
                <el-button size="mini" class="code-btn" @click="getverify();">{{btnText}}</el-button>
              </mt-field>
            </div>
          </div>
          <div class="btn-row" style="text-align: center;">
              <el-button type="danger" @click="phonelogin()"></el-button>
          </div>
          <div class="notice px-2 py-3">
            <span></span>
            <router-link to="/clause"></router-link>
          </div>
        </div>
      </mt-tab-container-item>
    </mt-tab-container>
  </div>
</template>

<script>
import { Toast } from "mint-ui";
const checkPhoneReg = /^((1[3,5,8][0-9])|(14[5,7])|(17[0,6,7,8])|(19[7]))\d{8}$/;
export default {
  name: "login",

  data() {
    return {
      model: {
        username: "",
        password: "",
      },
      
      selected: "0",
      phoneNumber: "",
      verify: "",
      area: false,
      validateCode: "",
      backNum: 0,
      btnText: "",
      validateSuccess: false,
      backUrl: false,
      checkPass: false
    };
  },
  watch: {
    selected() {
      this.model.username = "";
    }
  },
  methods: {
    async login() {

      if (this.model.username == "") {
        Toast("");
        return;
      }
      if (!checkPhoneReg.test(this.model.username)) {
        Toast("");
        return;
      }
      if (this.model.password == "") {
        Toast("");
        return;
      }
      //
      const res = await this.$http.post('login/signIn.do',this.model)
      console.log(res)
      if (res.data.code != 0) {
        console.log(res.data)
        localStorage.token = res.data.token;
        this.$router.push('/user')
        this.$store.commit('saveUser',res.data);
        this.$store.commit('saveToken',res.data.token);
        Toast('')
      }else {
        Toast('')
      }
    },
    checkPhone() {
      if (
        !checkPhoneReg.test(this.model.username) ||
        this.backNum > 0
      ) {
        this.checkPass = false;
        return "disabled";
      } else {
        this.checkPass = true;
        return "ok";
      }
    },
    phonelogin() {
      if (this.model.username == "") {
        Toast("");
        return;
      }
      if (!checkPhoneReg.test(this.model.username)) {
        Toast("");
        return;
      }
      if (this.verify == "") {
        Toast("");
        return;
      }
      if (this.verify != this.validateCode) {
        Toast("");
        return;
      }
      // const res = await this.$http.post('',this.user)
      //   if (data.code == "200") {

      //   } else {
      //     Toast(data.message);
          
      //   }
    },
    
    async getCode() {
      if (checkPhoneReg.test(this.model.username) && this.backNum == 0) {
        // 
        // const res = await this.$http.get(`/api/user/sendSms.do?phone=${this.phoneNumber}`);
        //   if (res.data.code == "200") {
        //     console.log(res)
        //     //
        //     this.user.code = res.data;
        //     this.backNum = 60;
        //     let inter = setInterval(() => {
        //       this.btnText = `(${this.backNum})`;
        //       this.backNum--;
        //       if (this.backNum == 0) {
        //         this.backNum = 0;
        //         clearInterval(inter);
        //       }
        //     }, 1000);

        //   } else {
        //     Toast(res.data.message);
        //   }
        
      }
    },
    getverify() {
      if (this.model.username == "") {
        Toast("");
        return;
      }
      if (!checkPhoneReg.test(this.model.username)) {
        Toast("");
        return;
      }
      if (this.backNum == 0) {
        this.getCode();
      }
    },
  },
  created() {
  }
};
</script>

<style scoped lang='scss'>
.mint-cell-wrapper {
  background-color: white;
}
.el-button {
  width: 98%;

}
</style>